import loadingTip from "../../components/common/loading-tip.vue"
import paperList from "../../components/paper-list/paper-list.vue"
export default {
	data() {
		return {
			isShow:false,
			papers:[
				{
					username:'低头看鱼',
					userpic:'../../static/demo/userpic/12.jpg',
					time:'13:58',
					noreadnum:'10',
					info:'赵丽颖《知否》一句话打脸。哈哈哈哈嘻嘻嘻'
					
				}
			]
		}
	},
	methods: {
		closeMenu() {
			this.isShow = false
		},
		openMenu() {
			this.isShow = true
		},
		tapHandle() {
			uni.navigateTo({
				url:'../user-chat/user-chat'
			})
		}
	},
	onReachBottom() {
		this.$refs.tip.changeLoadingText()
	},
	onPullDownRefresh() {
		setTimeout(()=>{
			let obj = {}
			uni.showToast({
				title:"刷新完成",
				position:"top"
			})
			uni.stopPullDownRefresh()
		},2000)
		
	},
	onNavigationBarButtonTap(e){
		if(e.index === 1){
			this.openMenu()
		}else if(e.index === 0){
			uni.navigateTo({
				url:'../friend-list/friend-list'
			})
		}
	},
	components:{
		loadingTip,
		paperList
	}
}
